	transition: all 0.6s;
	opacity: 0;
	-webkit-opacity: 0;
	-o-opacity: 0;
	-ms-opacity: 0;
	-moz-opacity: 0;
	visibility: hidden;

	filter:alpha(opacity=0);
}
.loader-layer.active[data-v-2bf7f472]{
	visibility:visible;
	opacity: 1;
	-webkit-opacity: 1;
	-o-opacity: 1;
	-ms-opacity: 1;
	-moz-opacity: 1;
	filter:alpha(opacity=100);
}
